.basic-grid {
  display: grid;
  gap: 0.5rem;
  grid-template-areas:
    'a a'
    'b c';
  grid-template-rows: 1fr 8fr;
  grid-template-columns: 4fr 6fr;
}

.fireship-card:nth-child(1) {
  grid-area: a;
  font-size: 2rem;
  align-items: left;
  padding-left: 1rem;
}

.fireship-card:nth-child(2) {
  /* picture */
  grid-area: b;
  padding: 10px 10px 10px 10px;
  align-items: center;
  background-image: url('./images/pfp.png');
  background-size: cover;
}

.fireship-card:nth-child(3) {
  grid-area: c;
  align-items: left;
  padding: 1em;
  font-size: 1em;
}

.fireship-card:nth-child(3) > p {
  margin-bottom: 0;
}

.fireship-card:nth-child(3) > hr {
  margin-bottom: 10px;
  margin-top: 0;
}

.fireship-card:nth-child(3) > *:not(hr) {
  margin-left: 3%;
}

.fireship-card {
  display: flex;
  flex-direction: column;
  background: rgba(0, 0, 0, 0.15);
  backdrop-filter: blur(5px);
  color: white;
  box-shadow: rgba(3, 8, 20, 0.1) 0px 0.15rem 0.5rem,
    rgba(2, 8, 20, 0.1) 0px 0.075rem 0.175rem;
  height: 100%;
  width: 100%;
  border-radius: 4px;
  transition: all 200ms;
  overflow: hidden;
}

.photo-grid {
  display: grid;
  gap: 1rem;
  width: 75%;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  grid-auto-rows: 240px;
}

.overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
  transition: 0.5s ease;
  background-color: #008cba;
}

.photo-grid > .card:hover .overlay {
  opacity: 1;
}

.photo-grid > .card {
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.text {
  color: white;
  font-size: 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  text-align: center;
}

.photo-grid > :nth-child(1) {
  background-image: url('./images/guitar.jpeg');
  background-size: cover;
}

.photo-grid > :nth-child(2) {
  background-image: url('./images/vert.png');
  background-size: cover;
}

.photo-grid > :nth-child(3) {
  background-image: url('./images/ieee.png');
  background-size: cover;
}

.photo-grid > :nth-child(4) {
  background-image: url('./images/danceoff.png');
  background-size: cover;
}

.photo-grid > :nth-child(5) {
  background-image: url('./images/alter.jpg');
  background-size: cover;
}

.photo-grid > :nth-child(6) {
  background-image: url('./images/water.jpeg');
  background-size: cover;
}

/* Medium screens */
@media screen and (min-width: 600px) {
  .card-tall {
    grid-row: span 2 / auto;
  }

  .card-wide {
    grid-column: span 2 / auto;
  }
}

