/** ACCORDION: https://codepen.io/uniqname/pen/gKmpz/ **/
/* The Magic */
.accordion:hover .accordion-item:hover .accordion-item-content,
.accordion .accordion-item--default .accordion-item-content {
  height: 5em;
}

.accordion-item-content,
.accordion:hover .accordion-item-content {
  height: 0;
  overflow: hidden;
  transition: height 0.25s;
}

/* Aesthetic */
body {
  background: #222;
}

.accordion {
  padding: 0;
  margin: 0 auto;
  width: 75%;
  max-width: 1140px;
}

.accordion .accordion-item {
  background-color: rgba(0, 0, 0, 0.15);
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  border-bottom: 1px solid #666;
  padding: 1em;
  color: #eee;
}

.accordion h2 {
  margin: 0;
}

.accordion h1 {
  color: white;
}

a {
  color: rgb(146, 202, 255);
}

.my-tooltip {
  position: relative;
  display: inline-block;
}

.my-tooltip .my-tooltiptext {
  visibility: hidden;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  background-color: rgba(255, 255, 255, 0.15);
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 5px 0;
  position: absolute;
  z-index: 1;
  top: -5px;
  left: 150%;
  white-space: nowrap;
  padding-left: 1em;
  padding-right: 1em;
}

.my-tooltiptext {
  font-size: small;
}

.my-tooltip .my-tooltiptext::after {
  content: '';
  position: absolute;
  top: 50%;
  right: 100%;
  margin-top: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: transparent rgba(255, 255, 255, 0.15) transparent transparent;
}
.my-tooltip:hover .my-tooltiptext {
  visibility: visible;
}

.overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
  transition: 0.5s ease;
}

.container:hover .overlay {
  opacity: 1;
}

.text {
  color: white;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}

.text:nth-child(1) {
  font-size: 15px !important;
}

/* The dropdown container */
.dropdown {
  float: left;
  overflow: visible;
}

/* Dropdown button */
.dropdown .dropbtn {
  font-size: 16px;
  border: none;
  outline: none;
  color: white;
  background-color: inherit;
  font-family: inherit; /* Important for vertical align on mobile phones */
  margin: 0; /* Important for vertical align on mobile phones */
}

.dropbtn:hover {
  text-decoration: none !important;
}

/* Dropdown content (hidden by default) */
.dropdown-content {
  display: none;
  position: absolute;

  background-color: rgba(0, 0, 0, 0.15);
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  border-radius: 5%;
  min-width: 100px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}

/* Links inside the dropdown */
.dropdown-content a {
  float: none;
  color: white;
  font-size: 12px;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  text-align: left;
}

/* Add a grey background color to dropdown links on hover */
.dropdown-content a:hover {
  background-color: rgba(0, 0, 0, 0.1);
  border-radius: 5%;
}

/* Show the dropdown menu on hover */
.dropdown:hover .dropdown-content {
  display: block;
}

.nav-item > a:hover {
  text-decoration: none !important;
}

.basic-grid {
  display: grid;
  grid-gap: 0.5rem;
  gap: 0.5rem;
  grid-template-areas:
    'a a'
    'b c';
  grid-template-rows: 1fr 8fr;
  grid-template-columns: 4fr 6fr;
}

.fireship-card:nth-child(1) {
  grid-area: a;
  font-size: 2rem;
  align-items: left;
  padding-left: 1rem;
}

.fireship-card:nth-child(2) {
  /* picture */
  grid-area: b;
  padding: 10px 10px 10px 10px;
  align-items: center;
  background-image: url(/tung-doan/static/media/pfp.b039a816.png);
  background-size: cover;
}

.fireship-card:nth-child(3) {
  grid-area: c;
  align-items: left;
  padding: 1em;
  font-size: 1em;
}

.fireship-card:nth-child(3) > p {
  margin-bottom: 0;
}

.fireship-card:nth-child(3) > hr {
  margin-bottom: 10px;
  margin-top: 0;
}

.fireship-card:nth-child(3) > *:not(hr) {
  margin-left: 3%;
}

.fireship-card {
  display: flex;
  flex-direction: column;
  background: rgba(0, 0, 0, 0.15);
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  color: white;
  box-shadow: rgba(3, 8, 20, 0.1) 0px 0.15rem 0.5rem,
    rgba(2, 8, 20, 0.1) 0px 0.075rem 0.175rem;
  height: 100%;
  width: 100%;
  border-radius: 4px;
  transition: all 200ms;
  overflow: hidden;
}

.photo-grid {
  display: grid;
  grid-gap: 1rem;
  gap: 1rem;
  width: 75%;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  grid-auto-rows: 240px;
}

.overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
  transition: 0.5s ease;
  background-color: #008cba;
}

.photo-grid > .card:hover .overlay {
  opacity: 1;
}

.photo-grid > .card {
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.text {
  color: white;
  font-size: 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}

.photo-grid > :nth-child(1) {
  background-image: url(/tung-doan/static/media/guitar.f57b6113.jpeg);
  background-size: cover;
}

.photo-grid > :nth-child(2) {
  background-image: url(/tung-doan/static/media/vert.100f584e.png);
  background-size: cover;
}

.photo-grid > :nth-child(3) {
  background-image: url(data:image/png;base64,);
  background-size: cover;
}

.photo-grid > :nth-child(4) {
  background-image: url(/tung-doan/static/media/danceoff.c76ad56c.png);
  background-size: cover;
}

.photo-grid > :nth-child(5) {
  background-image: url(/tung-doan/static/media/alter.489f4b52.jpg);
  background-size: cover;
}

.photo-grid > :nth-child(6) {
  background-image: url(/tung-doan/static/media/water.37e46108.jpeg);
