/** CSS TAKEN FROM https://github.com/do-community/React-With-Smooth-Scrolling/blob/master/src/App.css **/

.nav {
  background-color: rgba(255, 255, 255, 0.15);
  backdrop-filter: blur(5px);
  /* background: pink; */
  position: sticky;
  top: 0px;
  width: 100%;
  height: 80px;
  z-index: 1000;
  box-shadow: 0 4px 14px 0 rgba(0, 0, 0, 0.15);
}

.nav-logo {
  font-size: 30pt;
  font-family: 'Monoton', cursive;
}

.nav-logo:hover {
  cursor: pointer;
  text-decoration: none;
}

.nav {
  height: 4em;
}
.nav .nav-content {
  min-width: 70%;
  padding: 0rem 3rem;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
  color: white;
  text-shadow: 2px 2px 8px rgba(126, 125, 125, 0.781);
}

.nav-item {
  display: inline;
  margin-left: 2rem;
  float: left;
}

.nav-item:hover {
  cursor: pointer;
}

.section {
  display: flex;
  justify-content: center;
  align-items: center;
}

.section-dark {
  background: #333;
  color: white;
}

.profile-card {
  background-image: url('https://i.pinimg.com/originals/56/f9/e2/56f9e272de8708a62f2b94d941382415.jpg');
  background-repeat: no-repeat;
  background-size: cover;
}

#name-card {
  background-color: rgba(255, 255, 255, 0.25);
  backdrop-filter: blur(5px);
  box-shadow: 0 0 1rem 0 rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  color: white;
  text-shadow: 2px 2px 8px rgba(126, 125, 125, 0.781);
  height: fit-content;
  width: fit-content;
  padding-left: 10%;
  padding-right: 10%;
}
.section-content {
  max-width: 800px;
  margin: 0 auto;
  padding: 40px;
}
.nav-items {
  margin-bottom: auto;
  margin-top: auto;
}

.nav-item > .active,
.nav-item:not(.nav-logo):hover {
  font-weight: bold;
  text-shadow: 2px 2px 8px rgba(126, 125, 125, 0.781);
  text-decoration: none;
  color: white;
  font-size: larger;
}

/** https://codepen.io/chris22smith/pen/RZogMa **/
.bg {
  animation: slide 10s ease-in-out infinite alternate;
  background-image: linear-gradient(-60deg, #6c3 50%, #09f 50%);
  bottom: 0;
  left: -50%;
  opacity: 0.5;
  position: fixed;
  right: -50%;
  top: 0;
  z-index: -1;
}

.bg2 {
  animation-direction: alternate-reverse;
  animation-duration: 12s;
}

.bg3 {
  animation-duration: 15s;
}

@keyframes slide {
  0% {
    transform: translateX(-25%);
  }
  100% {
    transform: translateX(25%);
  }
}
