/** ACCORDION: https://codepen.io/uniqname/pen/gKmpz/ **/
/* The Magic */
.accordion:hover .accordion-item:hover .accordion-item-content,
.accordion .accordion-item--default .accordion-item-content {
  height: 5em;
}

.accordion-item-content,
.accordion:hover .accordion-item-content {
  height: 0;
  overflow: hidden;
  transition: height 0.25s;
}

/* Aesthetic */
body {
  background: #222;
}

.accordion {
  padding: 0;
  margin: 0 auto;
  width: 75%;
  max-width: 1140px;
}

.accordion .accordion-item {
  background-color: rgba(0, 0, 0, 0.15);
  backdrop-filter: blur(5px);
  border-bottom: 1px solid #666;
  padding: 1em;
  color: #eee;
}

.accordion h2 {
  margin: 0;
}

.accordion h1 {
  color: white;
}

a {
  color: rgb(146, 202, 255);
}

.my-tooltip {
  position: relative;
  display: inline-block;
}

.my-tooltip .my-tooltiptext {
  visibility: hidden;
  width: fit-content;
  background-color: rgba(255, 255, 255, 0.15);
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 5px 0;
  position: absolute;
  z-index: 1;
  top: -5px;
  left: 150%;
  white-space: nowrap;
  padding-left: 1em;
  padding-right: 1em;
}

.my-tooltiptext {
  font-size: small;
}

.my-tooltip .my-tooltiptext::after {
  content: '';
  position: absolute;
  top: 50%;
  right: 100%;
  margin-top: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: transparent rgba(255, 255, 255, 0.15) transparent transparent;
}
.my-tooltip:hover .my-tooltiptext {
  visibility: visible;
}

.overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
  transition: 0.5s ease;
}

.container:hover .overlay {
  opacity: 1;
}

.text {
  color: white;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  text-align: center;
}

.text:nth-child(1) {
  font-size: 15px !important;
}

/* The dropdown container */
.dropdown {
  float: left;
  overflow: visible;
}

/* Dropdown button */
.dropdown .dropbtn {
  font-size: 16px;
  border: none;
  outline: none;
  color: white;
  background-color: inherit;
  font-family: inherit; /* Important for vertical align on mobile phones */
  margin: 0; /* Important for vertical align on mobile phones */
}

.dropbtn:hover {
  text-decoration: none !important;
}

/* Dropdown content (hidden by default) */
.dropdown-content {
  display: none;
  position: absolute;

  background-color: rgba(0, 0, 0, 0.15);
  backdrop-filter: blur(5px);
  border-radius: 5%;
  min-width: 100px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}

/* Links inside the dropdown */
.dropdown-content a {
  float: none;
  color: white;
  font-size: 12px;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  text-align: left;
}

/* Add a grey background color to dropdown links on hover */
.dropdown-content a:hover {
  background-color: rgba(0, 0, 0, 0.1);
  border-radius: 5%;
}

/* Show the dropdown menu on hover */
.dropdown:hover .dropdown-content {
  display: block;
}

.nav-item > a:hover {
  text-decoration: none !important;
}
